import domStr from './dom'

import Csv from '../lib/csv'
import ExportCsv from '../lib/csvExport'

export default class X {
    constructor () {
        // 
        this.ticketMaxTry = 10
        // 
        this.autoExportWhenPause = false
        this.autoExportWhenStop = true
        // 
        this.play = false
        this.now = {
            taobaodingdan: {
                ready: false,
                value: []
            },
            ludanjilu: {
                ready: false,
                value: []
            }
        }
        // 
        this.rebind()
        // 
        $('#ajaxdata').before($(domStr))
        // 
        this.ids = [
            '630808830478',
            '630808830485',
            '630808830508',
            '630358323368',
            '630506310243',
            '630808830609',
            '630506310256',
            '630808830616',
            '630598531107',
            '630598531975',
            '630598531476',
            '630598532152',
            '630598531215',
            '630598531463',
            '630598531710',
            '630598531759',
            '630598531786',
            '630598531842'
        ]
        // IDindex
        this.idIndex = 0
        // 
        this.finish = []
        // 
        this.cache()
        this.register()
        // hack
        this.hackPage()
        // 
        // this.startSearch()
    }
    hackPage () {
        $('#Panel1').html('')
        $('.taskBar').hide()
    }
    // 
    rebind () {
        let _this = this
        $(document).off("click", ".menu li").on("click", ".menu li", function () {
            // 
            $(this).find("button").attr("disabled", "disabled").delay(100).animate({ disabled: '' });
            //  ztoAjax 
            var index = $(this).index();
            //   630644632616_0 
            var bill = $(this).find("button").attr("data-bill");
            // 
            var btnText = $(this).find("button").html()
            // 
            if (btnText === "" || btnText === "") { return }
            // 
            if (!$(this).hasClass("curr")) {
                // 
                var url = $(this).find("button").attr("data-url");
                var id = $(this).find("button").attr("data-id");
                var text = $(this).find("button").text().trim();
                var queryParms = getUrlParmas(url);
                var currentButton = this;
                // 
                const billQueryPreauthFn = () => {
                    return new Promise((resolve, reject) => {
                        let ticket = ''
                        let count = 1
                        const doIt = () => {
                            $('#log').text(`${_this.idIndex + 1} / ${_this.ids.length} ${queryParms.id} ${btnText} Ticket ${count} `)
                            ztosec.billQueryPreauth({
                                bill: queryParms.id,
                                billType: queryParms.type
                            }, function (params) {
                                ticket = params.ticket
                                $('#log').text(`${_this.idIndex + 1} / ${_this.ids.length} ${queryParms.id} ${btnText} Ticket ${count}  Ticket${ticket}`)
                                resolve(ticket)
                            })
                            setTimeout(() => {
                                if (ticket === '') {
                                    $('#log').text(`${_this.idIndex + 1} / ${_this.ids.length} ${queryParms.id} ${btnText} Ticket ${count} `)
                                    setTimeout(() => {
                                        if (count < _this.ticketMaxTry) {
                                            count += 1
                                            doIt()
                                        } else {
                                            reject()
                                        }
                                    }, 300)
                                }
                            }, 1000)
                        }
                        doIt()
                    })
                }
                billQueryPreauthFn()
                //  
                .then(ticket => {
                    $('#log').text(`${_this.idIndex + 1} / ${_this.ids.length} ${queryParms.id} ${btnText} `)
                    ztoAjax({
                        url: url + "&queryTicket=" + ticket,
                        type: "get",
                        data: "",
                        index: index,
                        bill: bill,
                        id: id,
                        text: text
                    });
                    $(currentButton).addClass("curr");
                    //   setTimeout 
                    setTimeout(() => {
                        $('#log').text(`${_this.idIndex + 1} / ${_this.ids.length} ${queryParms.id} `)
                        // 
                        switch (id) {
                            case 'taobaodingdan': {
                                _this.now.taobaodingdan.value = _this.getDingdanDataFromTable(queryParms.id)
                                _this.now.taobaodingdan.ready = true
                            };
                                break;
                            case 'ludanjilu': {
                                _this.now.ludanjilu.value = _this.getLudanDataFromTable(queryParms.id)
                                _this.now.ludanjilu.ready = true
                            };
                                break;
                            default:
                                console.log('')
                                break;
                        }
                        // 
                        const mixRes = _this.tryMix()
                        // 
                        if (mixRes) {
                            // 
                            _this.finish.push(mixRes)
                            // index1
                            _this.idIndex ++
                            //  this.now
                            _this.now = {
                                taobaodingdan: {
                                    ready: false,
                                    value: []
                                },
                                ludanjilu: {
                                    ready: false,
                                    value: []
                                }
                            }
                            //  
                            if (_this.play) {
                                if (_this.idIndex < _this.ids.length) {
                                    // 
                                    _this.startSearch()
                                } else {
                                    //  
                                    if (_this.autoExportWhenStop) {
                                        _this.exportCSV()
                                    }
                                    $('#log').text(`${_this.ids.length}`)
                                }
                            } else {
                                if (_this.autoExportWhenPause) {
                                    _this.exportCSV()
                                }
                                $('#log').text(`${_this.idIndex + 1} / ${_this.ids.length} ${queryParms.id} `)
                            }
                        }
                    }, 2000);
                })
                .catch(() => {
                    console.log('')
                })
            } else {
                $(this).removeClass("curr");
                var id = $(this).find("button").attr("data-id");
                if ($(this).find("button").html() != "") {
                    $("." + id).remove();
                } else {
                    $("." + id).removeClass("curr");
                }
            }
        })
    }
    // now
    tryMix () {
        const {
            taobaodingdan: {
                ready: tb_ready,
                value: tb_val
            },
            ludanjilu: {
                ready: ld_ready,
                value: ld_val
            }
        } = this.now
        if (tb_ready && ld_ready) {
            // 
            return Object.assign({}, tb_val[0], ld_val[0])
        } else {
            //  false 
            return false
        }
    }
    // 
    getDingdanDataFromTable (id) {
        let res = []
        const ul = $(`#route${id}_0`)
        const trs = ul.find('.curr.taobaodingdan table').children(1).children()
        for (let index = 1; index < trs.length; index++) {
            const tds = $(trs[index]).children()
            const row = {
                yundanbianhao: "\t" + tds[0].innerHTML,
                dingdanbianhao: "\t" + tds[1].innerHTML,
                dingdanshijian: tds[2].innerHTML,
                fajianrendianhua: tds[3].innerHTML,
                fajianrendizhi: tds[4].innerHTML,
                shoujianrendianhua: tds[5].innerHTML,
                shoujianrendizhi: tds[6].innerHTML,
                lanjianren: tds[7].innerHTML,
                shoujianwangdian: tds[8].innerHTML,
                dingdanlaiyuan: $(tds[9]).text()
            }
            res.push(row)
        }
        return res
    }
    // 
    getLudanDataFromTable (id) {
        let res = []
        const ul = $(`#route${id}_0`)
        const trs = ul.find('.curr.ludanjilu table').children(1).children()
        for (let index = 1; index < trs.length; index++) {
            const tds = $(trs[index]).children()
            const row = {
                pinming: tds[8].innerHTML,
                daishoukuan: tds[10].innerHTML
            }
            res.push(row)
        }
        return res
    }
    //  
    search (id = '') {
        $('#log').text(`${this.idIndex + 1} / ${this.ids.length} ${id} `)
        return new Promise((resolve, reject) => {
            $("#txtJobNoList").val(id)
            var txtbill = document.getElementById("txtJobNoList");
            var list = txtbill.value.trim().split("\n");
            ztosec.queryReport({ bill_ids: list }, function () {
                var billcode = $("#txtJobNoList").val();
                $("#Panel1,.xubox_layer").hide();
                var showdiv = $("#showdiv").val();
                var history = "";
                if (document.getElementById("chkzidou2").checked) {
                    history = "true";
                }
                $(".taskBar li,.xubox_layer").removeClass("curr");
                $(".docBubble").remove();
                var date1 = new Date();  //
                var loadLayer;
                if (isSearch) {
                    if (reqScanTips) {
                        reqScanTips.abort();
                        isSearch = true;
                    }
                    reqScanTips = $.ajax({
                        type: "post",
                        cache: false,
                        timeout: 60000,
                        url: "bills2.aspx",
                        data: {
                            Bill: billcode, showdiv: showdiv, history: history
                        },
                        beforeSend: function () {
                            isSearch = false;
                            $("#Button1").addClass("gray");
                            $("#sxubox_layer20").show();
                        },
                        // dataType: "html",
                        dataType: "jsonp",
                        jsonp: "callbackfun",
                        error: function (a, b, c) {
                            isSearch = true;
                            $("#Button1").removeClass("gray");
                            $("#sxubox_layer20").hide();
                            $("#ajaxdata").html("<div style=\"text-align:center; width:100%; line-height:150%;margin-top: 130px;\"><img src=\"/images/error.png\" width=\"150\" /><br/> QQ1299450042</div>");
                        },
                        success: function (rs) {
                            isSearch = true; 
                            $("#Button1").removeClass("gray");
                            if (rs.n == "" || rs.n == null) {
                                $("#ajaxdata").html("<div style=\"text-align:center; width:100%; line-height:150%;margin-top: 130px;\"><img src=\"/images/error.png\" width=\"150\" /><br/>,</div>");
                            } else {
                                if (rs.n.length < 300) {
                                    $("#ajaxdata").html("<div style=\"text-align:center; width:100%; line-height:150%;margin-top: 130px;\"><img src=\"/images/error.png\" width=\"150\" /><br/>" + rs.n + "</div>");
                                } else {
                                    $("#ajaxdata").html(rs.n);
                                    resolve()
                                }
                                dialogOnresizeparameters();
                            }
                            var date2 = new Date();    //
                            var date3 = date2.getTime() - date1.getTime();  //
                            $(".totalTime").html("" + date3 / 1000 + "");
                            $("#sxubox_layer20").hide();
                            setTimeout("getUserState()", 2000);//1  
                        },
                        complete: function () {
                            isSearch = true;
                            $("#sxubox_layer20").hide();
                        }
                    });
                }
            })
        })
    }
    // 
    cache () {
        // 
        this.$ZTO_input = $('#txtJobNoList')
        // 
        this.$panel = $('#panel')
        this.$panelToggleBtn = $('#panelToggleBtn')
        this.$uploader = $('#uploader')
        this.$helpButton = $('#helpButton')
        this.$startButton = $('#startButton')
        this.$pauseButton = $('#pauseButton')
        this.$goonButton = $('#goonButton')
        this.$downloadButton = $('#downloadButton')
    }
    // 
    register () {
        // 
        this.$panelToggleBtn.on('click', () => {
            if (this.$panel.is(":hidden")) {
                this.panelShow()
            } else {
                this.panelHide()
            }
        })
        // 
        this.$uploader.on('change', () => {
            const file = this.$uploader.get(0).files[0]
            const reader = new FileReader()
            reader.readAsText(file, 'utf-8')
            reader.onload = e => {
                this.ids = e.target.result.split("\n")
                $('#log').text(`${this.ids.length}  [] `)
            }
        })
        // 
        this.$startButton.on('click', () => {
            if (this.ids.length === 0) {
                alert('')
                return
            }
            this.play = true
            this.startSearch()
        })
        // 
        this.$pauseButton.on('click', () => {
            this.play = false
        })
        // 
        this.$goonButton.on('click', () => {
            if (this.ids.length === 0) {
                alert('')
                return
            }
            this.play = true
            this.startSearch()
        })
        // 
        this.$helpButton.on('click', () => {
            alert(`
1. 
2. 
3. 
4. 
5. 
            `.trim())
        })
        // 
        this.$downloadButton.on('click', () => {
            this.exportCSV()
        })
    }
    // 
    panelShow () {
        this.$panel.show()
        this.$panelToggleBtn.text('')
    }
    // 
    panelHide () {
        this.$panel.hide()
        this.$panelToggleBtn.text('')
    }
    // 
    startSearch () {
        const id = this.ids[this.idIndex]
        this.search(id).then(() => {
            // []
            $(`button[data-id='taobaodingdan'][data-bill='${id}_0']`)[0].click()
            // []
            setTimeout(() => {
                $(`button[data-id='ludanjilu'][data-bill='${id}_0']`)[0].click()
            }, 1000);
        })
    }
    // CSV
    exportCSV () {
        // 
        const _params = {
            columns: [
                {label: '', prop: 'yundanbianhao'},
                {label: '', prop: 'dingdanbianhao'},
                {label: '', prop: 'dingdanshijian'},
                {label: '()', prop: 'fajianrendianhua'},
                {label: '', prop: 'fajianrendizhi'},
                {label: '()', prop: 'shoujianrendianhua'},
                {label: '', prop: 'shoujianrendizhi'},
                {label: '', prop: 'lanjianren'},
                {label: '', prop: 'shoujianwangdian'},
                {label: '', prop: 'dingdanlaiyuan'},
                {label: '', prop: 'pinming'},
                {label: '', prop: 'daishoukuan'}
            ],
            data: this.finish,
            title: 'table',
            noHeader: false
        }
        // 
        const data = Csv(_params.columns, _params.data, {}, _params.noHeader)
        // 
        ExportCsv.download(_params.title, data)
    }
}